___CSS_LOADER_EXPORT___.push([module.id, `* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    /* outline: 2px solid red; */
}

body {
    height: 100vh;
    width: 100vw;
    display: grid;
    grid-template-columns: 0.5fr, repeat(2, 1fr);
    grid-template-rows: 100px 1fr;
    gap: 1em;
    padding: 1em;
}

h1 {
    grid-column: 1 / 4;
    display: flex;
    justify-content: center;
    align-items: center;
}
.sidebar {
    grid-column: 1/2;
}
.board1 {
    grid-column: 2/3;
    background-color: #987272;
}
.board2 {
    grid-column: 3/4;
    background-color: rgb(141, 141, 164);
}
.Board {
    padding:1em;
    display: flex;
    justify-content: center;
    align-items: center;
}

.Board > .play-area{
    height: 100%;
    width: auto;
    aspect-ratio: 1/1;
    display: grid;
    grid-template-columns: repeat(8, 1fr);
    grid-template-rows: repeat(8,1fr);
    gap:1px;
}

.play-area > div {
    background-color: #ccc; /* so you can see the squares */
    aspect-ratio: 1 / 1;
    outline: 2px solid white;
}
